@media print {
  table {
    width: 100% !important;
    border-collapse: collapse;
    margin-bottom: 0px !important;
  }
  .print_table {
    border: 0px;
    border-collapse: collapse;
  }

  .print_table td {
    border: none;
    outline: none;
  }

  .head {
    font-size: 20px;
  }

  .time {
    font-family: "Times New Roman", Times, serif;
    font-style: italic;
    font-weight: lighter;
    text-align: left;
  }

  .no-border th,
  .no-border td {
    border: none !important;
  }

  .dark-border th,
  .dark-border td {
    border: 1px solid black !important;
  }
  .light-border th,
  .light-border td {
    border: 1px solid rgb(149, 145, 145) !important;
  }

  .bg {
    background: lightgrey !important;
  }

  .firmname {
    padding: 5px 10px;
    margin: 0 auto;
    width: 70%;
    text-align: center;
    font-size: 20px !important;
    font-family: tahoma;
    font-weight: bold;
  }

  .address {
    font-weight: lighter;
    text-align: center;
  }

  .left {
    text-align: left !important;
  }

  .center {
    text-align: center !important;
  }

  .right {
    text-align: right !important;
  }

  .master_heading {
    font-weight: bold;
    text-align: left;
  }

  .underline {
    text-decoration: underline;
  }

  .bold {
    font-weight: bold;
  }

  .normal {
    font-weight: normal;
  }

  .master_field {
    font-weight: bold;
    text-align: left;
    font-weight: normal;
    text-decoration: underline;
  }

  .detail_heading {
    font-weight: bold;
  }

  .detail_field {
    font-weight: normal;
  }
}
